.header {
  background-color: transparent;
  padding: 5px 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-weight: 800;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 30px;
  color: aliceblue;
}

.buttonsGroupBox1 {
  margin: 0 auto;
  background-color: rgb(10, 25, 41);
  border-width: 1px 0px;
  border: 2px solid rgba(194, 224, 255, 0.08);
  width: 1100px;
  border-radius: 10px;
  margin-top: 20px;
  gap: 30px;
  text-align: center;
  padding: 44px;
  padding-top: 0;
}

.modalsGroupBox2 {
  margin: 0 auto;
  padding: 44px;
  padding-top: 0;
  background-color: rgb(10, 25, 41);
  border-width: 1px 0px;
  border: 2px solid rgba(194, 224, 255, 0.08);
  width: 1100px;
  border-radius: 10px;
  margin-top: 20px;
  text-align: center;
}

.title {
  color: white;
  font-size: 30px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  margin-bottom: 30px;
}

.box {
  gap: 30px;
  margin: auto;
  display: flex;
  justify-content: center;
}

