.modal{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.616);
  left: 0;
  width: 100vw;
  height: 100vh;
  
}

.modalTop{
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 300px;
  background-color: #fff;
  z-index: 2;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-x: scroll;
  animation-name: modalTop;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

@keyframes modalTop{
  0%{
    height: 0;
    padding: 0;
  }
  100%{
    
  }
}

.modalBottom{
  position: absolute;
  left: 0;
