.modalClose{
  width: 100vw;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: -1;
}


.displayNone{
  display: none;
}

.exit{
  background-repeat: no-repeat;
  border: none;
  background-color: transparent;
  background-position: center;
  width: 16px;
  height: 16px;
  background-image: url('../imgs/x.svg');
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 20px;
  &:hover{
    background-color: crimson;
    border-radius: 50%;
  }
}

::-webkit-scrollbar{
  background-color: rgb(10, 25, 41);
}

.modalRightEl{
  color: white;
  text-align: left;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  >h3{
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
  }
}